import React from "react"
import "../styles/burger.css"
import { slide as Menu} from "react-burger-menu";
import { Link } from "gatsby"

export default function Burger() {

    return (
        <Menu width={250}>
            <p> --- Menu --- </p>
            <Link className="menu-item" to="/">Home</Link>
            <Link className="menu-item" to="/today_predict_forcus"></Link>
            <Link className="menu-item" to="/today_predict"></Link>

            {/* <Link className="menu-item" to="/past_predict"></Link> */}

            <Link className="menu-item" to="/note">Note</Link>
            <Link className="menu-item" to="/about"></Link>
        </Menu>
    )
}

